import React, { createContext, useState } from 'react'
import './App.css';
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import Home from "./routes/Home";
import { Helmet, HelmetProvider } from 'react-helmet-async';
import SkylineDogPark from './routes/SkylineDogPark';
import LittleCloudPark from './routes/LittleCloudPark'
import NorthDomingoBacaDogPark from './routes/NorthDomingoBacaDogPark'
import QuintessencePark from './routes/QuintessencePark'
import WLJacksonPark from './routes/WLJacksonPark'

const App = (props) => {

    return (
        <HelmetProvider >
                <div className="container">
                    <Router>
                        <Routes>
                            <Route
                                exact path="/parks/little-cloud-park"
                                element={<LittleCloudPark />}
                            />
                            <Route
                                exact path="/parks/north-domingo-baca-dog-park"
                                element={<NorthDomingoBacaDogPark />}
                            />
                            <Route
                                exact path="/parks/quintessence-park"
                                element={<QuintessencePark />}
                            />
                            <Route
                                exact path="/parks/skyline-dog-park"
                                element={<SkylineDogPark />}
                            />
                            <Route
                                exact path="/parks/wl-jackson-park"
                                element={<WLJacksonPark />}
                            />
                            <Route
                                exact path="/"
                                element={<Home />}
                            />
                            <Route
                                exact path="/home"
                                element={<Home />}
                            />
                        </Routes>
                    </Router>
                </div>
        </HelmetProvider>
    );
};

export default App;
